.socials {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-self: center;

  &List {
    display: flex;
    gap: 70px;

    margin-bottom: 30px;

    @media (max-width: 400px) {
      gap: 30px;
    }
  }

  &LinkTo,
  &Email {
    font-size: 22px;
    color: #000;
    opacity: 0.6;

    transition: opacity 0.2s;
    &:hover {
      opacity: 0.8;
    }
  }

  &Email {
    letter-spacing: 0.05em;
    line-height: 100%;
  }
}
